"use strict";

import Namespace from './Namespace';
import JSObjectNamespace from './JSObjectNamespace';

export class NoopNamespace extends Namespace {

    constructor() {
        super();
    }

    callMethod(name, args) {
        return new JSObjectNamespace(null);
    }

    readAttribute(name) {
        return new JSObjectNamespace(null);
    }

    readKey(key) {
        return new JSObjectNamespace(null);
    }

}

export const NoopNamespaceInstance = new NoopNamespace()
